<template>
<div>
  <header>
    <div class = 'content'>
      <span></span>
      <dropDown class = 'typeDrop' :dropData = doorType @dropChange = 'typeChange'>
      </dropDown>

      <span></span>
      <el-input class = 'input' v-model = curWidth type = 'number' placeholder = ''></el-input>

      <span></span>
      <el-input class = 'input' v-model = curHeight type = 'number' placeholder = ''></el-input>

      <span></span>
      <dropDown class = 'typeDrop' :dropData = frameData @dropChange = 'frameChange'>
      </dropDown>

      <span></span>
      <el-input class = 'input' v-model = curNumber type = 'number' placeholder = ''></el-input>

      <el-button class = 'confirm' type = 'primary' @click = 'confirm'></el-button>
    </div>
  </header>

  <main>
    <el-button class = 'confirm' type = 'primary' @click = 'createPdf'>
       PDF 
    </el-button>
    <br><br>

    <div class = 'imgDiv' id = 'imgDivId' ref = 'imgDom'
      v-if = 'planData.length > 0'>
      <div style = 'width: 100%; height: 10px;'></div>
      <template v-for = '(data, index) in planData'>
        <div class = 'plan' :key = index>
          <template v-for = '(dat, ind) in data'>
            <div class = 'planTitle'  v-if = 'ind < 1'>
              <template v-for = '(da, ins) in dat'>
                <span>{{da.title}}{{da.content}}</span>&nbsp;&nbsp;&nbsp;
              </template>
            </div>

            <template v-for = '(d, i) in dat'>
              <div class = 'planDetail' v-if = 'ind > 0'>
                <span>{{d.title}}{{d.content}}</span>
              </div>
            </template>
          </template>
        </div>

        <div style = 'width: 800px; height:39px; background-color: #ffffff'
          v-if = '(index + 1) % 22 == 0'>
        </div>
      </template>
    </div>

  </main>
</div>
</template>

<script>
import dropDown from '@/components/dropDown'
import { doubleOpen } from '../../js/doubleOpen'
import html2canvas from 'html2canvas'
import JsPDF from 'jspdf'

const doorType = [
  { type: 'doubleOpen', title: ''},
  { type: 'singleOpen', title: ''}
]

const frameData = [
  { type: 0, title: ''},
  { type: 1, title: ''}
]

export default {
  name: 'index',

  components: { dropDown },

  data () {
    return {
      doorType: doorType,
      curType: doorType[0],
      curWidth: '',
      curHeight: '',
      curNumber: '',
      frameData: frameData,
      underFrame: false,

      planData: [],
    }
  },

  methods: {
    typeChange (value) {
      this.curType = value
    },

    frameChange (value) {
      this.underFrame = value.type;
    },

    confirm () {
      if(this.curWidth != '' && this.curHeight != '' && this.curNumber != '') {
        let plan =
          doubleOpen(this.underFrame,this.curWidth, this.curHeight, this.curNumber)
        this.planData.push(plan)
      }
    },

    createPdf () {
      window.pageYOffset = 0;
      document.documentElement.scrollTop = 0;
      document.body.scrollTop = 0;

      html2canvas(this.$refs.imgDom, {
        allowTaint: true,
        scale: 2,
        dpi: 182,//pdf
      }).then(function (canvas) {
          let contentWidth = canvas.width
          let contentHeight = canvas.height
          let pageHeight = contentWidth / 592.28 * 841.89
          let leftHeight = contentHeight
          let position = 0
          let imgWidth = 595.28
          let imgHeight = 592.28 / contentWidth * contentHeight
          let pageData = canvas.toDataURL('image/jpeg', 1.0)

          let PDF = new JsPDF('', 'pt', 'a4')
          if (leftHeight < pageHeight) {
            PDF.addImage(pageData, 'JPEG', 0, 0, imgWidth, imgHeight)
          } else {
            while (leftHeight > 0) {
              PDF.addImage(pageData, 'JPEG', 0, position, imgWidth, imgHeight)
              leftHeight -= pageHeight
              position -= 841.89
              if (leftHeight > 0) {
                PDF.addPage()
              }
            }
          }
          PDF.save('.pdf')
        }
      )

    }
  }
}
</script>

<style scoped>
header {
  width: 100%;
  min-width: 1920px;
  height: auto;
  color: #ffffff;
  border-bottom: 1px solid #dcdfe6;
}
header .content {
  width: 1920px;
  height: auto;
  margin: 0 auto;
  padding-top: 20px;
  padding-bottom: 20px;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  align-items: center;
}
header .content span {
  color: #000000;
  margin-left: 30px;
}
header .content .input {
  width: 150px;
  height: 40px;
}
header .content .confirm {
  width: 120px;
  height: 40px;
  margin-left: 40px;
}
main {
  width: 100%;
  height: auto;
  padding-top: 20px;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  align-items: center;
  -webkit-flex-direction: column;
  flex-direction: column;
}
.imgDiv {
  width: 800px;
  height: auto;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0 20px 20px 20px;
}
main .plan {
  width: 400px;
  height: auto;
  box-sizing: border-box;
  /*margin-top: 10px;*/
  margin-bottom: 10px;
  border: 1px dashed #000000;
  padding: 10px;
}
main .plan .planTitle {
  width: 100%;
  text-align: center;
  float: left;
  margin-bottom: 10px;
}
main .plan .planDetail {
  width: 50%;
  float: left;
}
</style>



