
function doubleOpen(underFrame,width, height, number) {
  let bFold = (width - 90) / 2;             //B

  let bWidth = bFold + 68;                  //B
  let aWidth = bWidth + 25;                 //A
  let cWidth = bFold + 120;                 //C
  let dWidth = cWidth - 77;                 //D

  let acHeight = !underFrame? height - 31 : height - 65;   //
  let bdHeight = acHeight - 3;

  return [
    [
      {title: '', content: width + ' * ' + height},
      {title: '', content: number},
      {title: '', content: !underFrame? '' : ''}
    ],
    [
      {title: 'A', content: aWidth + ' * ' + acHeight + ' * ' + number},
      {title: 'B', content: bWidth + ' * ' + bdHeight + ' * ' + number},
      {title: 'C', content: cWidth + ' * ' + acHeight + ' * ' + number},
      {title: 'D', content: dWidth + ' * ' + bdHeight + ' * ' + number},
    ]
  ]

}

export { doubleOpen }



